.BookCover {
    margin: 10px;
    width: 300px;
    min-height: 350px;
}

.cover {
  box-shadow: 0 0 5px -1px black, inset -1px 1px 2px rgba(255, 255, 255, 0.5);
  margin: auto;
  height: 350px;
}

.brightness05 {
    filter: brightness(0.5);
}

@media (prefers-color-scheme: dark) {
    .cover {
        box-shadow: 0 0 5px -1px #f5ebe7, inset -1px 1px 2px black;
    }
}